nav {
    background-color: rgb(235, 229, 199);
    
}

nav .brand-logo {
    color: black;
    font-family:'Pacifico', cursive;
}


/* Media */

@media screen and (max-width: 768px) {
    nav .brand-logo {
      font-size: 0.75rem;
    }
  
    .brand-logo a {
      font-size: 0.75rem;
    }
  
  }
  
  @media screen and (max-width: 450px) {
    .brand-logo {
      font-size: 0.5rem;
    }
  
  
  }
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-image: url(/Click-Game/static/media/books.b39e78c0.jpg);
    background-repeat: repeat;
    background-size: contain;
    /* position: fixed; */
    min-height: 400px;
}

h1, h2 {
    text-align: center;
    font-family:'Pacifico', cursive;
    color: whitesmoke;
    text-shadow: 1px 1px 2px black, 0 0 25px gray, 0 0 5px darkgreen;
    
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 225px;
    margin: 1rem;
    position: relative;
    width: 225px;
    box-shadow: 0 3px 6px rgb(66, 65, 65), 0 3px 6px rgb(66, 65, 65);
    transition: box-shadow .25s;
    border-radius: 2px;
  }

  .card-image img {
    height: 225px;
    width: 225px;
  }

  .hoverable {
    transition: box-shadow .25s;
    border-radius: 2px;

  }

